.datacard {
  width: 20%;
  height: 100%;
  margin: 5px 10px;
  text-align: center;
  box-shadow: 1px 1px 12px 1px gray;
}
#confirmed {
  color: orange;
}

#recovered {
  color: green;
}

#deceased {
  color: red;
}
.count {
  font-size: 20px;
  font-weight: bolder;
}

.header {
  font-size: 12px;
}
