.stategrid {
  display: grid;
  grid-template-columns: 20vw 20vw 20vw 20vw;
  grid-template-rows: 6vh;
}

td {
  padding: 0px;
  width: 25%;
  background-color: rgb(245, 247, 252);
  border-radius: 5px;
  height: 10px;
  text-align: center;
  font-size: 18px;
}

td > p {
  font-size: 12px;
}

sub {
  font-size: 10px;
}

tr:not(thead > tr) {
  width: 90vw;
  box-shadow: 1px 1px 12px rgb(187, 185, 185);
}

table {
  border-collapse: separate;
  border-spacing: 2px 10px;
  width: 50%;
}

.fa-arrow-up {
  color: red;
}

.fa-arrow-down {
  color: green;
}
.recovered {
  color: green;
}
.active {
  color: orange;
}
.deaths {
  color: red;
}
